@import 'variables';

html {
	font-size: 100%;
	height: 100%;
	overflow-x: hidden;
	margin: 0px;
	padding: 0px;
	touch-action: manipulation;
  }
  
  body {
	font-size: 16px;
	font-family: "Poppins", serif;
	width: 100%;
	height: 100%;
	margin: 0;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	word-wrap: break-word;
	color: #333;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  a,
  ul,
  span,
  li,
  img,
  inpot,
  button {
	margin: 0;
	padding: 0;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
	line-height: 1.5;
	font-weight: inherit;
  }
  
  h1,
  h2,
  h3 {
	font-family: "Poppins", cursive;
  }
  
  p {
	line-height: 1.6;
	font-size: 1em;
	font-weight: 400;
	color: #777;
  }
  
  h1 {
	font-size: 4em;
	line-height: 1;
  }
  h2 {
	font-size: 2.2em;
	line-height: 1.1;
  }
  h3 {
	font-size: 1.8em;
  }
  h4 {
	font-size: 1.3em;
  }
  h5 {
	font-size: 1em;
  }
  h6 {
	font-size: 0.9em;
	letter-spacing: 1px;
  }
  
  a,
  button {
	display: inline-block;
	text-decoration: none;
	color: inherit;
	line-height: 1;
	-webkit-transition: all 0.25s ease-in-out;
	transition: all 0.25s ease-in-out;
  }
  
  a:focus,
  a:active,
  a:hover,
  button:focus,
  button:active,
  button:hover,
  a b.light-color:hover {
	text-decoration: none;
	color: $mainClr;
  }
  
  b {
	font-weight: 600;
  }
  
  img {
	max-width: 100%;
  }
  
  li {
	list-style: none;
	display: inline-block;
  }
  
  span {
	display: inline-block;
  }
  
  button {
	outline: 0;
	border: 0;
	background: none;
	cursor: pointer;
  }
  
  .icon {
	font-size: 1.1em;
	display: inline-block;
	line-height: inherit;
  }
  
  [class^="icon-"]:before,
  [class*=" icon-"]:before {
	line-height: inherit;
  }
  
  b.max-bold {
	font-weight: 700;
  }
  
  a {
	color: $secClr;
	text-decoration: none;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
  }
  a:hover,
  a:focus {
	color: $mainClr;
	text-decoration: none;
  }
  
  .bg-green {
	background-color: $greenLight;
  }

  .bg-green-dark {
	background-color: $mainGreen;
  }
  
  .bg-blue {
	background-color: #deebf7;
  }
  
  .bg-silver {
	background-color: #e7e7e7;
  }
  
  .project img {
	border: 1px solid #d7d7d7;
	padding: 10px;
	margin-top: 10px;
	margin-bottom: 10px;
	background-color: #fff;
  }
  
  /* SECTIONS */
  .section {
	padding: 100px 0 50px;
	border-bottom: 1px solid #ddd;
  }
  
  .section .heading {
	margin-bottom: 60px;
  }
  
  .section .heading:before {
	content: "";
	height: 10px;
	width: 50px;
	border-radius: 5px;
	margin-bottom: 20px;
	display: block;
	background: $mainClr;
  }
  
  .margin-t-10 {
	margin-top: 10px;
  }
  
  .margin-b-30 {
	margin-bottom: 30px;
  }
  .margin-b-50 {
	margin-bottom: 50px;
  }
  .margin-tb-30 {
	margin-top: 30px;
	margin-bottom: 30px;
  }
  
  .font-green {
	color: $mainGreen;
  }
  
  .font-semi-white {
	color: #ddd;
  }
  
  .font-lite-black {
	color: #999;
  }
  .bb-green {
	border-bottom: 4px solid $mainClr;
  }
  
  /* HEADER */
  .header-section {
	padding: 50px 0;
	position: relative;
	background-size: cover;
	z-index: 1;
	background-image: url(../assets/images/bg-content.jpg);
  }
  .header-section:before {
	content: "";
	z-index: -1;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.2);
  }
  .header-section .info {
	margin-bottom: 10px;
  }
  .header-section .info .icon {
	float: left;
	font-size: 26px;
  }
  .header-section .info .right-area {
	margin-left: 45px;
  }
  .header-section .info-location h5 {
	margin-top: 6px;
	position: relative;
	top: 8px;
	left: -10px;
  }
  
  /* INTRO */
  .intro {
	color: #fff;
  }
  
  .intro .profile-img {
	height: 160px;
	width: 160px;
	border-radius: 100px;
	margin: 0 auto;
	overflow: hidden;
	border: 4px solid #fff;
  }
  
  .intro .information > li {
	display: block;
	margin: 5px 0;
  }
  
  .intro .social-icons > li > a,
  .intro .social-icons > li > button {
	height: 50px;
	line-height: 50px;
	width: 50px;
	border-radius: 100px;
	overflow: hidden;
	z-index: 1;
	position: relative;
	margin: 0 5px 5px;
	text-align: center;
	background: $darkGreen;
	color: #fff;
	font-size: 22px;
  }
  
  .intro .social-icons > li > a:after,
  .intro .social-icons > li > button:after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	z-index: -1;
	border-radius: 100px;
	background: $mainClr;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	-webkit-transform: scale(0);
	transform: scale(0);
  }
  
  .intro .social-icons > li > a:hover:after,
  .intro .social-icons > li > button:hover:after {
	-webkit-transform: scale(1);
	transform: scale(1);
  }
  
  .intro .downlad-btn {
	padding: 15px 30px;
	border-radius: 4px;
	float: right;
	margin-top: 30px;
	box-shadow: inset 0 0px 4px rgba(0, 0, 0, 0.3);
	background: $mainClr;
	color: #fff;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
  }
  
  .intro .downlad-btn:hover {
	-webkit-transform: translateY(-4px);
	transform: translateY(-4px);
  }
  
  .btn-article {
	background: #53b713;
	color: #fff;
	padding: 15px 35px 15px 35px;
  }
  
  .btn-article:hover {
	background: $mainClr;
	color: #fff;
  }
  
  .radial-progress {
	position: relative;
	max-width: 200px;
	margin: 0 auto;
  }
  
  .radial-progress .progressbar-text {
	font-size: 2.2em;
	font-weight: 500;
	padding-bottom: 25px !important;
	color: #333 !important;
  }
  
  .radial-progress .progressbar-text:after {
	content: "%";
  }
  
  .radial-progress .progress-title {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 100%;
	letter-spacing: 0;
	text-align: center;
	transform: translate(-50%, 15px);
	color: #777;
  }
  
  .experience .list > li {
	display: block;
	position: relative;
	margin-bottom: 10px;
	padding-left: 20px;
  }
  
  .experience .list > li:before {
	content: "";
	position: absolute;
	top: 50%;
	left: 0;
	height: 10px;
	width: 10px;
	margin-top: -5px;
	display: block;
	border-radius: 10px;
	border: 2px solid #555;
	opacity: 0.4;
  }
  
  .portfolio-section {
	position: relative;
  }
  .portfolio-section .portfolioFilter {
	margin-top: 60px;
	color: #666;
  }
  .portfolio-section .portfolioFilter a {
	margin: 10px 10px 0 0;
  }
  .portfolio-section .portfolioFilter a.current {
	color: $mainClr;
  }
  .portfolio-section .portfolioContainer {
	margin-left: -15px;
	margin-right: -15px;
  }
  .portfolio-section .p-item {
	width: 25%;
	padding: 0 7.5px 0px;
	margin-top: 15px;
  }
  .portfolio-section .p-item a {
	display: inline-block;
	position: relative;
  }
  
  .education-wrapper {
	position: relative;
  }
  .education-wrapper:after {
	content: "";
	position: absolute;
	top: 8px;
	bottom: 0;
	left: -51px;
	width: 2px;
	background: #ccc;
  }
  .education-wrapper .education {
	position: relative;
  }
  .education-wrapper .education:after {
	content: "";
	position: absolute;
	top: 8px;
	left: -58px;
	width: 16px;
	height: 16px;
	border-radius: 10px;
	z-index: 1;
	box-shadow: 0 0 0 8px rgba(93, 202, 253, 0.4);
	background: #5b9bd5;
  }
  
  .note-section {
	padding: 50px 0;
	position: relative;
	text-align: center;
	z-index: 1;
  }
  .note-section:before {
	content: "";
	z-index: -1;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
  }
  .note-section .icon {
	margin-bottom: 20px;
	height: 40px;
	width: 40px;
	line-height: 40px;
	border-radius: 30px;
	background: $mainClr;
	color: #fff;
  }
  
  .quoto-section {
	padding: 80px 0;
	position: relative;
	background-size: cover;
	text-align: center;
	z-index: 1;
	color: #fff;
  }
  
  .quoto-section:before {
	content: "";
	z-index: -1;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.8);
  }
  
  .quoto-section .icon {
	margin-bottom: 20px;
	height: 40px;
	width: 40px;
	line-height: 40px;
	border-radius: 30px;
	background: $mainClr;
	color: #fff;
  }
  
  .copyright {
	text-align: center;
	padding: 50px 0;
	background: #0f3e6a;
	color: #fff;
  }
  
  /* scroll to top */
  #toTop {
    width: 100px;
    text-align: center;
    padding: 5px;
    position: fixed;
    bottom: 10px;
    right: 10px;
    cursor: pointer;
    display: none;
    font-size: 11px;
    z-index: 999;
}
  
.bg-center {
	text-align: center;
	margin:auto;
}

