  @import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,700%7CAllura");
  
  /* ---------------------------------
  MEDIA QUERIES
  --------------------------------- */
  @media (max-width: 1024px) {
	.intro .profile-img {
	  margin-bottom: 20px;
	}
	.intro .social-icons {
	  margin-top: 10px;
	  margin-bottom: 10px;
	  margin-left: 10px;
	}
  }
  
  @media only screen and (max-width: 992px) {
	.education-wrapper:after,
	.education-wrapper .education:after {
	  display: none;
	}
  }
  
  @media (max-width: 768px) {
	.intro .profile-img {
	  margin-bottom: 10px;
	  text-align: center;
	}
	.intro .profile-info h2,
	.intro .profile-info h4 {
	  text-align: left;
	}
  }
  
  @media only screen and (max-width: 767px) {
	p {
	  line-height: 1.4;
	}
	h1 {
	  font-size: 3em;
	}
	h2 {
	  font-size: 2em;
	}
	h3 {
	  font-size: 1.8em;
	}
	.section {
	  padding: 50px 0 0px;
	}
	.section .heading {
	  margin-bottom: 30px;
	}
  
	.intro .profile-info h2,
	.intro .profile-info h4 {
	  text-align: center;
	}
	.header-section .info-location {
	  text-align: center;
	}
	.header-section .ion-android-pin {
	  display: none;
	}
  
	.portfolio-section .portfolioFilter {
	  margin-top: 0px;
	  margin-bottom: 20px;
	}
	.portfolio-section .p-item {
	  width: 50%;
	}
  }
  
  @media only screen and (max-width: 575px) {
	body {
	  font-size: 14px;
	}
	.portfolio-section .p-item {
	  width: 100%;
	}
  }
  
  @media only screen and (max-width: 480px) {
	.intro .profile-img {
	  margin-bottom: 22px;
	}
	.intro .social-icons {
	  margin-top: 10px;
	  margin-bottom: 10px;
	  margin-left: 0;
	}
	.intro .profile-info h2,
	.intro .profile-info h4 {
	  text-align: center;
	}
  }
  
  .scroll {
	opacity: 0.3;
	background-color: aqua;
	width: 40px;
	height: 40px;
	position: fixed;
	bottom: 10px;
	right: 10px;
	border-radius: 5px;
	border: none;
  }
  
  .scroll:hover {
	opacity: 1;
  }
  
  .arrow-up {
	color: white;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-top: -9px;
	margin-left: -5px;
  }
  
  button:focus {
	outline: none;
	cursor: pointer;
  }
  
  .button-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: center;
	margin-top: 10px;
  }
  
  .button-container .btn {
	margin-right: 10px;
	margin-bottom: 10px;
  }

  .text-underline {
  text-decoration: underline !important
  }